export default {
    layout: "Emp-content",
    data() {
        return {
            department: {
                type: "",
                department: "",

            },
            errors: [],
        };
    },
    methods: {
        addDepartment() {

            this.$axios
                .$post("/department/add", this.department)
                .then((res) => {
                    this.department = "";
                    this.errors = "";
                    this.$toaster.success(res.message);
                    this.$router.push("/employee/department");
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    console.log(this.errors);
                });
        },
    },

};
